'use client';

import { useState, useEffect } from 'react';
import ProductList from '../components/ProductList';
import Cart from '../components/Cart';

export default function Home() {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [orderNumber, setOrderNumber] = useState(null);
  const [orderStatus, setOrderStatus] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const response = await fetch('http://oms-backend.test/api/orders');
        if (!response.ok) {
          throw new Error('Failed to fetch products');
        }
        const data = await response.json();
        setProducts(data); // Assuming the API returns an array of products
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, []);

  const addToCart = (product, quantity) => {
    const existingItem = cart.find(item => item.product_id === product.id);
    if (existingItem) {
      setCart(cart.map(item =>
        item.product_id === product.id
          ? { ...item, quantity: item.quantity + quantity }
          : item
      ));
    } else {
      setCart([...cart, {
        product_id: product.id,
        unit_price: product.price,
        quantity
      }]);
    }
  };

  const updateCartItem = (productId, quantity) => {
    if (quantity <= 0) {
      setCart(cart.filter(item => item.product_id !== productId));
    } else {
      setCart(cart.map(item =>
        item.product_id === productId
          ? { ...item, quantity }
          : item
      ));
    }
  };

  const calculateTotal = () => {
    return cart.reduce((total, item) => total + item.unit_price * item.quantity, 0);
  };

  const createOrder = async () => {
    const newOrderNumber = `ORD-${Date.now()}-0001`; // Generate order number
    const payload = {
      total_amount: calculateTotal(),
      status: 'created',
      order_number: newOrderNumber,
      items: cart
    };

    try {
      const response = await fetch('http://oms-backend.test/api/orders', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      if (response.ok) {
        setOrderNumber(newOrderNumber);
        setOrderStatus('created');
        alert('Order created successfully!');
      } else {
        alert('Failed to create order');
      }
    } catch (error) {
      console.error('Error creating order:', error);
      alert('Error creating order');
    }
  };

  const checkout = async () => {
    if (!orderNumber) {
      alert('Please create an order first');
      return;
    }

    const payload = {
      order_number: orderNumber,
      status: 'confirmed'
    };

    try {
      const response = await fetch('http://oms-backend.test/api/orders', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      if (response.ok) {
        setOrderStatus('confirmed');
        alert('Order confirmed!');
      } else {
        alert('Failed to confirm order');
      }
    } catch (error) {
      console.error('Error confirming order:', error);
      alert('Error confirming order');
    }
  };

  const cancelOrder = async () => {
    if (!orderNumber) {
      alert('No order to cancel');
      return;
    }

    const payload = {
      order_number: orderNumber,
      status: 'cancelled'
    };

    try {
      const response = await fetch('http://oms-backend.test/api/orders', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      if (response.ok) {
        setOrderStatus('cancelled');
        setCart([]);
        setOrderNumber(null);
        alert('Order cancelled!');
      } else {
        alert('Failed to cancel order');
      }
    } catch (error) {
      console.error('Error cancelling order:', error);
      alert('Error cancelling order');
    }
  };

  if (loading) return <div className="container mx-auto p-4">Loading products...</div>;
  if (error) return <div className="container mx-auto p-4">Error: {error}</div>;

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-8">E-Commerce Store</h1>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div>
          <h2 className="text-2xl font-semibold mb-4">Products</h2>
          <ProductList products={products} addToCart={addToCart} />
        </div>
        
        <div>
          <h2 className="text-2xl font-semibold mb-4">Cart</h2>
          <Cart 
            cart={cart} 
            updateCartItem={updateCartItem} 
            total={calculateTotal()} 
            orderStatus={orderStatus}
            createOrder={createOrder}
            checkout={checkout}
            cancelOrder={cancelOrder}
          />
        </div>
      </div>
    </div>
  );
}